import React from 'react'
import {Col, Row} from "antd";

const Titulo = (props) => {

    const {tieneFiltro, tieneTitulo, tieneIcono, titulo} = props;

    return (
        <Row>
            {
                tieneIcono
                ?<Col xl={1} md={1} sm={1} xs={1}>
                    <i className="icon icon-shopping-cart gx-fs-xxl gx-ml-2 gx-d-inline-flex gx-vertical-align-middle"/>
                </Col>
                :null
            }
            <Col xl={23} md={23} sm={23} xs={23}>
                <h2 className={tieneTitulo ?`gx-entry-title gx-flex-row` :`gx-flex-row`}>
                    {
                        tieneTitulo
                        ?titulo
                        :null
                    }
                    {
                        tieneFiltro == true
                        ?<span className="gx-text-black gx-fs-md gx-pointer gx-ml-auto  gx-d-sm-block">
                            <select style={{border:'none', background:'transparent', marginLeft:'10px'}}>
                                <option>Sucursales</option>
                                <option>01</option>
                            </select>
                            <select style={{border:'none', background:'transparent', marginLeft:'10px'}}>
                                <option>Meses</option>
                                <option>2</option>
                            </select>
                            <select style={{border:'none', background:'transparent', marginLeft:'10px'}}>
                                <option>Ao</option>
                                <option>2</option>
                            </select>
                        </span>
                        :null
                    }
                </h2>
            </Col>
        </Row>
    )
}

export default Titulo
