import React from 'react'
import {Col, Row, Tooltip} from "antd";

const CardImagen = () => {
    return (
        <div className={`gx-product-item gx-product-vertical'`}>
            <div className="gx-product-image">
                <div className="gx-grid-thumb-equal">
                    <span className="gx-link gx-grid-thumb-cover">
                        <div style={{width:'100%', height:'100%', backgroundImage: "url('https://cdn.pixabay.com/photo/2015/05/26/12/31/baby-784609_1280.jpg')", backgroundSize: 'cover'}} >
                            <Row style={{ paddingTop:'5%'}}>
                                <Col md={24} xl={24} sm={24} xs={24}>
                                    <div className="gx-text-center" >
                                        <div className="gx-flex-row gx-justify-content-center gx-mb-3 gx-mb-sm-1">
                                            <span
                                                className={`gx-size-80 gx-border gx-border-white gx-text-red gx-flex-row gx-justify-content-center gx-align-items-center gx-rounded-circle`}
                                                style={{background:'#fff', padding:'10px'}}
                                            >
                                                <img alt="Remy Sharp" src={"https://cdn4.iconfinder.com/data/icons/kids-and-toys-1/32/Kids__Kids_Baby_Diaper_Childhood_Nappy-512.png"}/> 
                                            </span>
                                        </div>
                                        <strong className="gx-text-black">{"Infant"}</strong>
                                    </div>
                                </Col>
                            </Row>
                        </div>
                    </span>
                </div>
            </div>

            <div className="gx-line-indi-info">
                <div className={`gx-line-indi`} style={{background:'#F2F2F2', width: '100%', height: 10, margin: '10px', borderRadius: 50}}>
                    <Tooltip title="30% ">
                        <div className={`gx-line-indi`} style={{ background:'#FC8D49', width: '30%', height: 10, borderRadius: 50 }} />
                    </Tooltip>
                </div>
            </div>

            <div className="gx-product-body">
                <Row style={{'padding-bottom':'15px'}}>
                    <Col xl={12} sm={12} xl={12} xs={12}>
                        <div className="gx-text-center">
                            <strong>{"Real"}</strong>
                        </div>
                    </Col>
                    <Col xl={12} sm={12} xl={12} xs={12}>
                        <strong style={{'background': '#F2F2F2', 'padding':'5px'}} >{"S/ XXX"}</strong>
                    </Col>
                </Row>
                <Row style={{'padding-bottom':'15px'}}>
                    <Col xl={12} sm={12} xl={12} xs={12}>
                        <div className="gx-text-center">
                            <strong>{"Obj"}</strong>
                        </div>
                    </Col>
                    <Col xl={12} sm={12} xl={12} xs={12}>
                        <strong style={{'background': '#F2F2F2', 'padding':'5px'}} >{"S/ XXX"}</strong>
                    </Col>
                </Row>
                <Row>
                    <Col xl={12} sm={12} xl={12} xs={12}>
                        <div className="gx-text-center">
                            <strong>{"To Go"}</strong>
                        </div>
                    </Col>
                    <Col xl={12} sm={12} xl={12} xs={12}>
                        <strong style={{'background': '#F2F2F2', 'padding':'5px'}} >{"S/ XXX"}</strong>
                    </Col>
                </Row>
            </div>
        </div>
    )
}

export default CardImagen
