import React from 'react'
import './estilos/estiloImagenHover.css'
import {Col, Row} from "antd";

const ImagenHover = () => {
    return (
        <div class="contenedorImgHover">
            <figure>
                <span className="gx-link gx-grid-thumb-cover">
                    <div style={{height:'100%', backgroundImage: "url('https://cdn.pixabay.com/photo/2015/05/26/12/31/baby-784609_1280.jpg')", backgroundSize: '100% 100%', backgroundRepeat:'no-repeat'}} >
                        <Row style={{ paddingTop:'15%'}}>
                            <Col md={24} xl={24} sm={24} xs={24}>
                                <div className="gx-text-center" >
                                    <div className="gx-flex-row gx-justify-content-center gx-mb-3 gx-mb-sm-1">
                                        <span
                                            className={`gx-size-80 gx-border gx-border-white gx-text-red gx-flex-row gx-justify-content-center gx-align-items-center gx-rounded-circle`}
                                            style={{background:'#fff', padding:'10px'}}
                                        >
                                            <img alt="Remy Sharp" src={"https://cdn4.iconfinder.com/data/icons/kids-and-toys-1/32/Kids__Kids_Baby_Diaper_Childhood_Nappy-512.png"}/> 
                                        </span>
                                    </div>
                                    <strong className="gx-text-black">{"Infant"}</strong>
                                </div>
                            </Col>
                        </Row>
                    </div>
                </span>
                <div class="capa">
                    <img alt="Remy Sharp" style={{width:"30%", height:"30%"}} src={"https://cdn4.iconfinder.com/data/icons/kids-and-toys-1/32/Kids__Kids_Baby_Diaper_Childhood_Nappy-512.png"}/> 
                    <h2>Wipes</h2>
                    <p>Promocin</p>
                    <string style={{color:'white'}}>total: 5</string>
                </div>
            </figure>
        </div>
    )
}

export default ImagenHover
