import React from 'react'
import {Col, Row} from "antd";
import Auxiliary from "util/Auxiliary";
import CardImagen from 'components/Admin/CardImagen'
import Titulo from 'components/Admin/Titulo'

const SellIn = () => {
    return (
        <Auxiliary>
            <Row>
                <Col xl={24} md={24} sm={24} xs={24}>
                    <Titulo 
                        tieneFiltro = {true}
                        tieneTitulo = {true}
                        tieneIcono  = {true}
                        titulo      = {"Sell In"}
                    />  
                </Col>
                {
                    [{}, {}, {}, {}].map( function(item){
                        return (
                            <Col xl={6} md={8} sm={12} xs={24}>
                                <CardImagen />
                            </Col>
                        )
                    })
                }

                <Col xl={24} md={24} sm={24} xs={24}>
                    <Titulo 
                        tieneFiltro = {false}
                        tieneTitulo = {true}
                        tieneIcono  = {true}
                        titulo      = {"Sell Out"}
                    />  
                </Col>
                {
                    [{}, {}, {}, {}].map( function(item){
                        return (
                            <Col xl={6} md={8} sm={12} xs={24}>
                                <CardImagen />
                            </Col>
                        )
                    })
                }


            </Row>
        </Auxiliary>
    )
}

export default SellIn
